





















export default {
	data(){
		return {
			submitList:[],
			orderInfo:{
				account:'',
				orderStatus:'1'
			}
		};
	},
	onLoad:function() {
		uni.showLoading({
			mask:true,
			title:'loading'
		})
		var that = this;
		this.orderInfo.account = uni.getStorageSync('userInfo').account;
		const jsonString = {
			orderInfo:this.orderInfo,
			requestType:"getOrdeFromAccount",
		}
		const param ={
			controllerRequestType:"orderControllerService",
			jsonString:JSON.stringify(jsonString)
		}
		uni.request({
			url:this.websiteUrl,
			method:'POST',
			header:{
				"content-type":"application/x-www-form-urlencoded"
			},
			data:param,
			success(res) {
				that.submitList = [];
				if(res.data.errorCode == '0000'){
					const data = res.data.orderInfo.ordeList;
					data.forEach(function(value,index){
						if(value.taskInfo){
							that.submitList.push(value);
						}
					})
					uni.hideLoading();
				 }else{
					that.submitList = [];
					uni.showToast({
						icon:'none',
						title:res.data.errorMessage
					})
				}
			},
			fail(res) {
				uni.showToast({
					icon:'none',
					title:'网络异常，请稍后重试'
				})
			}
		})
	},
	methods:{
		goSubmitContent(task){
			uni.navigateTo({
				url:'./submitContent?task='+JSON.stringify(task)
			})	
			
		}
	}
}



